@charset "UTF-8";
body, div, dl, dt, dd, ul
, ol, li, h1, h2, h3, h4, h5
, h6, pre, code, form, fieldset
, legend, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0; }

body {
  font-size: 16px;
  font-family: "Microsoft YaHei"; }

ul, ol, li {
  list-style: none; }

u, a {
  text-decoration: none;
  font-size: 16px; }

i, em {
  font-style: normal; }

/* 清除默认加粗样式 */
b, strong {
  font-weight: normal; }

h1, h2, h3, h4, h5, h6 {
  font-size: 16px;
  font-weight: normal; }

img {
  border: none;
  display: block; }

input {
  border: none;
  outline: none; }

textarea {
  outline: none;
  border: none;
  resize: none; }

.fl {
  float: left; }

.fr {
  float: right; }

/* 边框样式 */
.bd-none {
  border: none !important; }

.bdl-none {
  border-left: none !important; }

.bdr-none {
  border-right: none !important; }

.bdt-none {
  border-top: none !important; }

.bdb-none {
  border-bottom: none !important; }

.radiu-none {
  border-radius: 0 !important; }

.tryTop {
  width: 0;
  height: 0;
  border-left-color: transparent !important;
  border-right-color: transparent !important;
  border-top-color: transparent !important; }

.tryBtn {
  width: 0;
  height: 0;
  border-bottom-color: transparent !important;
  border-left-color: transparent !important;
  border-right-color: transparent !important; }

/* 背景样式 */
.bg-none {
  background-image: none !important; }

/* 填充样式 */
.pdr-none {
  padding-right: 0 !important; }

.pdl-none {
  padding-left: 0 !important; }

.mgl-none {
  margin-left: 0 !important; }

.mgr-none {
  margin-right: 0 !important; }

/* 元素样式 */
.among {
  margin: 0 auto; }

.H {
  overflow: hidden; }

.clearBoth::after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden; }

.boxFull {
  width: 100%;
  height: 100%; }

.fs12 {
  font-size: 12px; }

.fs14 {
  font-size: 14px; }

.fs16 {
  font-size: 16px; }

.fs18 {
  font-size: 18px; }

.fs20 {
  font-size: 20px; }

.fs22 {
  font-size: 22px; }

.fs24 {
  font-size: 24px; }

.fwbold {
  font-weight: bold; }
